function updateUrl(param) {

var id = "";

  data.filter(function (i,n){

          //console.log(i.link,n);

          if (i.link == param) {
            //console.log(i.link);
            //console.log('item is: ' + n);
            id = n;
          }

      });

if (id) {
    return "?id="+id;
} else {
    console.log('No id match for: ' + param);
    return '#';

}


}

